        description: `Location data (e.g., GPS) enable a plethora of useful location-based application (e.g., Yelp). These application use location data to infer meaningful users' characteristics and behaviors, such as interest, favorite places, and jogging route. To create such applications, developers need a way to effectively test how applications react to different location patterns. We create TraceViz, a tool that provides a set of filters for finding GPS traces, and a natural 'brushing' interaction for selecting traces. Preliminary evaluation shows that users can use TraceViz to efficiently find location traces needed for testing a sample location-based application.`,
        status: `published`,
        details_link: `traceviz-location-data-brush/`,
