            <p><strong>Problem Solving:</strong> We created TraceViz, a tool that provided a set of filters for finding GPS traces, and a natural 'brushing' interaction for selecting traces. Users could select location traces that pass through a segment of a path (e.g., walking on State street from a coffee shop to a dormitory) by painting the segment using the map interface.</p>

