        title: `Enjoyable Training to Improve Visual-Motor Integration for Children with Autism`,
        description: `Some children with autism lack visual-motor integration ability. Traditional treatment activities are often time-consuming and thus prevent extensive training. We designed a computer game, called CuttingGame, to create a playful experience that both assesses and trains children in visual-motor integration to assist occupational therapists and the children.`,
